import params from '@/js/params'

const network = (() => {
	// 
	const request = function({
		url,
		method = 'get',
		data = {},
		header = {},
		jsonFormat = false,
	}) {

		if (!jsonFormat) {
			header['content-type'] = 'application/x-www-form-urlencoded';
		}

		return new Promise((resolve, reject) => {
			uni.request({
				url: params.hostName + url,
				method: method,
				data: data,
				header: header,
				success: res => {
					resolve(res);
				},
				fail: err => {
					reject(err);
				}
			});
		})
	}

	return {
		request
	}
})();





const media = (() => {
	// 
	const img = (function() {
		/**
		 * , h5bug
		 *  19.4.17
		 * 
		 * @params {object} obj
		 * @params {number} obj.count 		9
		 * @params {array}  obj.sizeType 	original compressed 
		 * @params {array}  obj.sourceType 	album camera 
		 */
		const upload = function(obj = {}) {
			return new Promise((resolve, reject) => {
				uni.chooseImage({
					count: obj.count,
					sizeType: obj.sizeType,
					sourceType: obj.sourceType,
					success: res => resolve(res),
					fail: err => reject(err)
				});
			})
		}

		// 
		const save = function(filePath) {
			return new Promise((resolve, reject) => {
				uni.saveImageToPhotosAlbum({
					filePath,
					success: res => resolve(res),
					fail: err => reject(err)
				});
			})
		}

		// 
		const preview = function() {}

		// 
		const getInfo = function(src) {
			return new Promise((resolve, reject) => {
				uni.getImageInfo({
					src,
					success: res => resolve(res),
					fail: err => reject(err)
				});
			})
		}

		return {
			upload,
			save,
			preview,
			getInfo
		}
	})()

	return {
		img
	}
})();





const file = (() => {
	// 
	const save = function(path) {
		return new Promise((resolve, reject) => {
			uni.saveFile({
				tempFilePath: path,
				success: res => resolve(res),
				fail: err => reject(err)
			});
		})
	}

	// 
	const getSaveFileList = function() {
		return new Promise((resolve, reject) => {
			uni.getSavedFileList({
				success: res => resolve(res),
				fail: err => reject(err)
			});
		})
	}

	// 
	const getSaveFileInfo = function(filePath) {
		return new Promise((resolve, reject) => {
			uni.getSavedFileInfo({
				filePath,
				success: res => resolve(res),
				fail: err => reject(err)
			});
		})
	}

	/**
	 * 
	 * 
	 * @param {string} file 
	 */
	const removeSaveFile = function(file) {
		return new Promise((resolve, reject) => {
			uni.removeSavedFile({
				filePath: file,
				success: res => resolve(res),
				fail: err => reject(err)
			});
		})
	}

	// 
	const getInfo = function(filePath) {
		return new Promise((resolve, reject) => {
			uni.getFileInfo({
				filePath,
				success: res => resolve(res),
				fail: err => reject(err)
			});
		})
	}

	return {
		save,
		getInfo,
		getSaveFileList,
		getSaveFileInfo,
		removeSaveFile
	}
})();






// 
const route = (() => {
	const redirectTo = function(path) {
		uni.redirectTo({
			url: path
		});
	}

	const navigateTo = function(path) {
		uni.navigateTo({
			url: path
		});
	}

	const navigateBack = function(num) {
		uni.navigateBack({
			delta: num
		});
	}

	const reLaunch = function(path) {
		uni.reLaunch({
			url: path
		});
	}

	const getCurrentPages = function() {
		console.log(getCurrentPages())
	}

	return {
		redirectTo,
		navigateTo,
		navigateBack,
		reLaunch
	}
})();






const system = (() => {
	// 
	const info = function() {
		return new Promise(resolve => {
			uni.getSystemInfo({
				success: res => resolve(res)
			});
		})
	}

	const canUse = function(str) {
		return uni.canIUse(str);
	}

	// 
	const copy = (function() {
		const get = function() {
			return new Promise((resolve, reject) => {
				uni.getClipboardData({
					success: res => resolve(res),
					fail: () => reject()
				});
			})
		}

		/**
		 * @params {string} data 
		 */
		const set = function(data) {
			return new Promise((resolve, reject) => {
				uni.setClipboardData({
					data,
					success: () => resolve(),
					fail: () => reject()
				});
			})
		}

		return {
			get,
			set
		}
	})()

	return {
		info,
		canUse,
		copy,
	}
})()






const canvas = (() => {
	/**
	 *  ctx
	 * 
	 * @params {string} id cavtag id
	 * @params {object} that ()
	 */
	const create = function(id, that) {
		return uni.createCanvasContext(id, that);
	}

	// 
	const cavToImg = function({
		x = 0,
		y = 0,
		id,
		width,
		height,
		destWidth, // cav
		destHeight,
		quality= 0.1 // 
	}) {
		return new Promise((resolve, reject) => {
			uni.canvasToTempFilePath({
				x,
				y,
				width,
				height,
				destWidth,
				destHeight,
				canvasId: id,
				quality,
				success: res => resolve(res),
				fail: err => reject(err)
			})
		})
	}

	// 
	const pixel = (function() {
		const get = function({
			x = 0,
			y = 0,
			id,
			width,
			height
		}, that) {
			return new Promise((resolve, reject) => {
				uni.canvasGetImageData({
					canvasId: id,
					x,
					y,
					width,
					height,
					success: res => resolve(res),
					fail: err => reject(err)
				}, that);
			});
		}

		const set = function({
			x = 0,
			y = 0,
			id,
			data,
			width,
			height
		}, that) {
			return new Promise((resolve, reject) => {
				uni.canvasPutImageData({
					canvasId: id,
					x,
					y,
					width,
					height,
					data,
					success: res => resolve(res),
					fail: err => reject(err)
				}, that)
			});
		}

		return {
			get,
			set
		}
	}).call()

	return {
		create,
		cavToImg,
		pixel
	}
})();







const ui = (() => {
	// 
	const showToast = function({
		title = 'title',
		icon = null,
		image = null,
		mask = false,
		duration = 1500,
		position = null
	} = {}) {
		return new Promise((resolve, reject) => {
			uni.showToast({
				title,
				icon,
				image,
				mask,
				duration,
				success: res => resolve(res),
				fail: err => reject(err)
			});
		})
	}

	return {
		showToast
	}
})();







const plugins = (() => {
	//  
	const getProvider = function() {
		return new Promise((resolve, reject) => {
			uni.getProvider({
				service: 'oauth',
				success: res => {
					resolve(res.provider[0])
				},
				fail: err => reject(err)
			});
		})
	}

	// 
	const login = async function() {
		const provider = await getProvider();

		return new Promise((resolve, reject) => {
			uni.login({
				provider,
				success: res => resolve(res),
				fail: err => reject(err)
			});
		})
	}

	// 
	const checkSession = function() {
		return new Promise((resolve, reject) => {
			uni.checkSession({
				success: res => resolve(res),
				fail: err => reject(err)
			})
		})
	}

	// 
	const userInfo = async function() {
		const provider = await getProvider();

		return new Promise((resolve, reject) => {
			uni.getUserInfo({
				provider,
				success: res => resolve(res),
				fail: err => reject(err)
			});
		})
	}

	return {
		login,
		checkSession,
		userInfo
	}
})();







const storage = (() => {
	/**
	 *  key 
	 * 
	 * @param {string} key 
	 */
	const get = function(key) {
		return new Promise((resolve, reject) => {
			uni.getStorage({
				key,
				success: res => resolve(res.data),
				fail: err => reject(err)
			});
		})
	}

	/**
	 *  key  key 
	 * 
	 * @param {string} key 
	 * @param {string} val 
	 */
	const set = function(key, val) {
		return new Promise((resolve, reject) => {
			uni.setStorage({
				key,
				data: val,
				success: res => resolve(res),
				fail: err => reject(err)
			});
		})
	}

	//  storage 
	const getInfo = function() {
		return new Promise((resolve, reject) => {
			uni.getStorageInfo({
				success: res => resolve(res),
				fail: err => reject(err)
			});
		})
	}

	/**
	 *  key
	 * 
	 * @param {string} key 
	 */
	const remove = function(key) {
		return new Promise((resolve, reject) => {
			uni.removeStorage({
				key,
				success: res => resolve(res),
				fail: err => reject(err)
			});
		})
	}

	//  
	const clear = function() {
		uni.clearStorage();
	}

	return {
		get,
		set,
		getInfo,
		remove,
		clear
	}
})();





export default {
	network,
	media,
	file,
	route,
	system,
	ui,
	canvas,
	plugins,
	storage
}
