import wxCharts from '@/wxcomponents/wx-charts/wxcharts.js';

export default {
	// , created
	init() {









	},

	// 
	load() {
		const {
			id,
			pixelRatio,
			width,
			height,
			data: {
				series
			}
		} = this.charts;
		
		if(Object.prototype.toString.call(series) !== '[object Array]' || series.length === 0){
			throw 'series';
		}
		
		return new wxCharts({
			canvasId:id,
			type: 'pie',
			fontSize: 11,
			legend: true,
			background: '#ffffff',
			pixelRatio,
			series,
			animation: true,
			width: width * pixelRatio,
			height: height * pixelRatio,
			dataLabel: true
		});
	},

	/**
	 * 
	 * 
	 * @param {object} exampleObj wxCharts
	 * @param {array} series charts		
	 */
	updateData(exampleObj, series) {
		//
		exampleObj.updateData({
			series
		});
	}
}
